$background_color: #5d5fef;
$lighter_shade: #ffffff;
$background_shade: #fafbfc;

.natco_list {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-direction: column;
  // padding-top: 60px;
  // border: 5px solid red;

  .nacto_list_filters {
    width: 100%;
    height: 10%;
    // border: 2px solid red;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0px 10px;
    .filter_options {
        // border: 2px solid red;
      width: 80%;
      height: fit-content;
      display: flex;
      justify-content: flex-start;
      align-items: center;
      gap: 15px;
      select {
        // padding: 9px 13px;
        // border-radius: 3px;
        // font-size: 1vw;
        color: #737791;
        cursor: pointer;
        font-size: 0.9vw;
        outline: none;
        border: 1px solid rgb(57, 56, 56);
        padding: 8px 12px;
        border-radius: 3px;
        border: none;
        box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px;
      }

      .jira_id_search_bar {
        width: fit-content;
        input {
          padding: 8px 15px;
          border-radius: 3px;
          font-size: 1vw;
          outline: none;
          border: none;
          width: fit-content;
          // border: 1px solid rgb(57, 56, 56);
          box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px;
        }
      }
    }
    .filter_button-natco-list {
      height: fit-content;
      width: fit-content;
      border-radius: 3px;
      display: flex;
      justify-content: center;
      align-items: center;
      gap: 10px;
    //   border: 1px solid red;
     
        background-color: $background_shade;
       
      button {
        padding: 8px 15px;
        cursor: pointer;
        border: none;
        border-radius: 3px;
        font-size: 1vw;
        background-color: $lighter_shade;
        color: #737791;
        font-weight: 500;
        box-shadow: rgba(67, 71, 85, 0.27) 0px 0px 0.25em,
        rgba(90, 125, 188, 0.05) 0px 0.25em 1em;
      }
      button:hover {
        background-color: $background_color;
        color: $background_shade;
      }
    }
  }

  .natco_list_table_section {
    width: 100%;
    height: 80%;
  }

  .pagination {
    width: 100%;
    height: 10%;
  }
}

@media screen and (max-width: 2559px) and (min-width: 1920px) {
  .natco_list {
    // padding-top: 80px;
  }
}

@media screen and (min-width: 2560px) {
  .natco_list {
    // padding-top: 80px;
  }
}
