$background_color: #5d5fef;
$lighter_shade: #ffffff;
$background_shade: #fafbfc;
.inner_pagination {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 5px;

  .prev_button {
    button {
      width: 30px;
      height: 30px;
      display: flex;
      justify-content: center;
      align-items: center;
       background-color: $background_color;
      color: white;
      cursor: pointer;
      border-radius: 5px;
      font-size: 1rem;
      border: none;
      box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px, rgba(60, 64, 67, 0.15) 0px 1px 3px 1px;
    }
  }

  .page_number{
       width: 30px;
    height: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
     background-color: $lighter_shade;
    color: $background_color;
    cursor: pointer;
    // border-radius: 50%;
    border-radius: 5px;
    font-size: 1rem;
    border: none;
    box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px, rgba(60, 64, 67, 0.15) 0px 1px 3px 1px;
  }

  .page_number_s{
   
    width: 30px;
    height: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 1px solid $background_color;
    color: $background_color;
    background-color:#F2EFE5 ;
    cursor: pointer;
    border-radius: 5px;
    box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px, rgba(60, 64, 67, 0.15) 0px 1px 3px 1px;
    font-size: 1rem;
  }

  .next_button{
    button {
        width: 30px;
    height: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
     background-color: $background_color;
    color: white;
    cursor: pointer;
    border-radius: 5px;
    font-size: 1rem;
    border: none;
    box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px, rgba(60, 64, 67, 0.15) 0px 1px 3px 1px;
      }
  }
}



@media screen and (max-width:2559px) and (min-width:1920px) {
  .inner_pagination{
      .prev_button{
        button{
          width: 50px;
          height: 50px;
          font-size: 1vw;
        }
      }
      .page_number{
        width: 50px;
          height: 50px;
          font-size: 1vw;
      }
      .page_number_s{
        width: 50px;
          height: 50px;
          font-size: 1vw;
      }
      .next_button{
        button{
          width: 50px;
          height: 50px;
          font-size: 1vw;
        }
      }
  }
}

@media screen and (min-width:2560px) {
  .inner_pagination{
    .prev_button{
      button{
        width: 50px;
        height: 50px;
        font-size: 1vw;
      }
    }
    .page_number{
      width: 50px;
        height: 50px;
        font-size: 1vw;
    }
    .page_number_s{
      width: 50px;
        height: 50px;
        font-size: 1vw;
    }
    .next_button{
      button{
        width: 50px;
        height: 50px;
        font-size: 1vw;
      }
    }
}
}