.natco_list_table{
    width: 100%;
    height: 100%;
   
    
    .inner_natco_list_table{
        padding: 5px;
        width: 100%;
        height: 100%;
        display: flex;
        justify-content: center;
        align-items: flex-start;

        table {
            // width: 99%;
            // height: fit-content;
            // // min-height: 98%;
            // border-collapse: collapse;
            background-color: white;
            width: 100%;
            border-color: #E8E8E8;
            height: 100%;
            overflow: hidden;
            border-radius: 5px;
            border-collapse: collapse;
            box-shadow: #dfe5f9 0px 1px 3px 0px, #dfe5f9 0px 0px 0px 1px;
            thead {
              height: fit-content;
              // border: 2px solid red;
              tr {
                th {
                  padding: 2.5vh 1vw;
                  font-size: 0.9vw;
                  font-weight: 500;
                 
                  input {
                    width: 100%;
                    height: 100%;
                    padding: 0px 5px;
                    border: none;
                    outline: none;
                  }
                }
              }
            }
  
            tbody {
              width: 100%;
              tr {
             
                td {
                  padding: 1vh 1vw;
                text-align: justify;

                font-size: 0.9vw;
                height: fit-content;
                }
                .select {
                  // text-align: center;
                  //   //  display: flex;
                  //   //  justify-content: center;
                  //   //  align-items: center;
                  //   //  gap: 4px;
                  //    height: 100%;
                  //    outline: none;
                  //    border-collapse:collapse ;
  
                  p {
                    display: inline-block;
                  }
                }
  
                #name {
                  cursor: pointer;
                }
              }
            }
          }
    }
}


@media screen and (max-width:2559px) and (min-width:1920px) {
    .natco_list_table{
        .inner_natco_list_table{
            table {
                thead {
                  tr {
                    th {
                      padding: 2.1vh 1vw;
                    }
                  }
                }
    
                tbody {
                  tr {
                    td {
                      padding: 2vh 1vw;
                    }
                  }
                }
              }
        }
    }
}

@media screen and (min-width:2560px) {
    .natco_list_table{
        .inner_natco_list_table{
            table {
                thead {
                  tr {
                    th {
                      padding: 2.3vh 1vw;
                    }
                  }
                }
    
                tbody {
                  tr {
                    td {
                      padding: 2.2vh 1vw;
                    }
                  }
                }
              }
        }
    }
}